import { useState } from "react";
import "./styles.css";

export default function App() {
  const [volume, setVolume] = useState("");
  const [weight, setWeight] = useState("");

  let a = volume;
  let b = weight;

  function liquorRatio(a, b) {
    let vol = a / b;
    return vol;
  }
  let ram = liquorRatio(a, b);

  return (
    <div className="App">
      <h1 id="title" style={{ textAlign: "center" }}>
        Converter
      </h1>
      <form>
        <label id="volume-label" htmlFor="volume">
          Volume of water in Litres(L)
        </label>
        <input
          type="number"
          placeholder="Enter volume of water"
          min="1"
          max="1000"
          id="vol"
          value={volume}
          onChange={(e) => setVolume(e.target.value)}
        />
        <br />
        <label id="weight-label" htmlFor="weight">
          Weight of fabric in Kilograms(Kg)
        </label>
        <input
          type="number"
          placeholder="Enter weight of fabric"
          min="1"
          max="5000"
          id="wei"
          value={weight}
          onChange={(e) => setWeight(e.target.value)}
        />
      </form>

      <h2>Liquor Ratio:{ram ? ram : 0} </h2>
    </div>
  );
}
